<template>
  <div>
    <!-- vue-js-modalの表示および設定 -->
    <modal name="first-modal"
    class="first-modal"
    :draggable="true"
    :resizable="false"
    :adaptive="true"
    >
      <!-- モーダルの内容物 -->
      <div class="modal-body">
        <!-- 初回ロード画面コンポーネントの読み込み
        モーダル内に配置しないとトランジションが機能しない-->
        <Loading v-if="isLoading"></Loading>
        <!-- 動画部分 -->
        <video src="@/assets/background.mp4"
        controls
        preload="auto"></video>
        <!-- ウィンドウを閉じるボタン -->
        <button @click="hide">閉じる</button>
      </div>  
    </modal>

    <!-- テスト用モーダル表示ボタン -->
    <!-- <button @click="show()">モーダルテスト</button> -->
  </div>
</template>

<script>
import Loading from '../atoms/Loading.vue'

export default {
  // モジュール名をエクスポート
  name: 'FirstViewModal',
  data:()=>({
    isLoading: true,
  }),
  // 初回ロード時にモーダルを表示する
  mounted(){
    this.$modal.show('first-modal');
    setTimeout(() => {
      this.isLoading = false
    }, 3000)
  },
  // モーダルを閉じるメソッド
  methods: {
    hide : function () {
      this.$modal.hide('first-modal');
    },
  },
  components:{
    Loading
  }
}
</script>

<style scoped>

.first-modal{
  background-color: rgba(0,0,0,.5);
  width: 100vw;
  height: 100vh;
  z-index: 1001;
}

.modal-body{
      position: absolute;
      z-index: 1000;
      width: 100%;
      height: 100%;
      display: flex;
      flex-flow: column;
      align-items: center;
      justify-content: center;
}

video{
  max-width: 75%;
  height: auto;
}
</style>
