import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Introduction from '../views/Introduction.vue'
import Story from '../views/Story.vue'
import Cast from '../views/Cast.vue'
import Staff from '../views/Staff.vue'
import Comment from '../views/Comment.vue'
import Movie from '../views/Movie.vue'
import Theater from '../views/Theater.vue'
import News from '../views/News.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/introduction',
    name: 'introduction',
    component: Introduction
  },
  {
    path: '/story',
    name: 'story',
    component: Story
  },
  {
    path: '/cast',
    name: 'cast',
    component: Cast
  },
  {
    path: '/staff',
    name: 'staff',
    component: Staff
  },
  {
    path: '/comment',
    name: 'Comment',
    component: Comment
  },
  {
    path: '/movie',
    name: 'movie',
    component: Movie
  },
  {
    path: '/theater',
    name: 'theater',
    component: Theater
  },
  {
    path: '/news',
    name: 'news',
    component: News
  },
  { // 存在しないURLの場合、HomeViewへリダイレクト
    path:'*',
    redirect:'/'
  }
]

const router = new VueRouter({
  mode: 'history',
  base: '/shirakawa.github.io/03/',
  routes
})

export default router
