<template>
  <!-- 初回ロード画面に関するコンポーネント -->
  <transition name="load" appear>
  <div v-if="isLoading">
    <div class="fullview">
      <div class="loading-spacer"></div>
        <vue-loading
          type="spin"
          color="#16b8f3"
          :size="{ width: '100px', height: '100px' }"
          />
      </div>
    </div>
  </transition>
</template>

<script>
// vue-loading-templateのインポート
import { VueLoading } from 'vue-loading-template'

export default {
  name: 'Loading',
  data () {
    return {
      isLoading: true
    }
  },
  components: {
    VueLoading
  }
}
</script>

<style>
.load-enter,
.load-leave-to{
  opacity: 0;
}


.load-leave-active{
  transition: opacity 3s;
}

.load-leave-from,
.load-enter-to{
  opacity: 1;
}

.fullview {
  width: 100%;
  height: 100%;
  background:black;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3000;
}
.loading-spacer {
  height: 40%;
}
</style>
